import React from 'react';
import Box from '@mui/material/Box';
import Grid from "@mui/material/Grid";

import Select from "@material-ui/core/Select";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";



import {
  getallSemesters,
  getSemesterWiseStudentList
} from "../api/utils";

import BarChart from "./BarChart";
import TableView from './TableView'
import Heatmap from "./Heatmap";
import Button from "@mui/material/Button";
import Modal from '@mui/material/Modal';



export default class TeacherDashboard extends React.Component {
  tableListRef = React.createRef();
  barchartRef = React.createRef();
  heatmapRef = React.createRef();

  constructor(props) {
    super(props);
    this.state = {
      semesterdata: [{'Semester_Name': 'Select One', 'Semester_ID': 100000}],
      selectedSemester: {},

      ////////// All States for Student Table
      studentListData:[],
      gridColDef: [],

      ///////////////Heatmep States
      isTeacherView:false,
      hData: [],
      open: false,

    };

    this.onSemesterChange = this.onSemesterChange.bind(this);
    this.onClickBack = this.onClickBack.bind(this);
    this.onLoad = this.onLoad.bind(this);
    this.onStudentClick = this.onStudentClick.bind(this);
    this.onReload = this.onReload.bind(this);
  }

  onReload(semesterid){
    console.log("Teacher Dashboard - On Reload")
    var that = this

    getallSemesters().then((res) => {
      if (res.status === 200) {
        var data = res.data;
        data = [ {'Semester_Name': 'Select One', 'Semester_ID': 100000}, ...data]
        that.setState({
          semesterdata: data,
          isTeacherView:true,
          selectedSemester: semesterid
        })
      }
    })

    getSemesterWiseStudentList(semesterid).then(semesterResponse => {
      if(semesterResponse.status === 200){
        var semesterData = semesterResponse.data;
        that.setState({
          studentListData:semesterData,
          selectedSemester:semesterid,

        },()=>{
          var viewFlag = "OverallTest"
          that.tableListRef.current.onSemesterChange(semesterData, semesterid);
          that.barchartRef.current.onSemesterChange([viewFlag, semesterid]);
          that.heatmapRef.current.onSemesterChange(semesterid);
        })
      }
    })

  }

  onLoad(){
    var that = this;
    getallSemesters().then((res) => {
      if (res.status === 200) {
        var data = res.data;
        var defaultSemid = data[0]['Semester_ID']
        debugger
        data = [ {'Semester_Name': 'Select One', 'Semester_ID': 100000}, ...data]
        that.setState({
          semesterdata: data,
          isTeacherView:true,
          selectedSemester: defaultSemid
        },()=>{
          getSemesterWiseStudentList(defaultSemid).then(semesterResponse => {
            if(semesterResponse.status === 200){
              var semesterData = semesterResponse.data;
              that.setState({
                studentListData:semesterData,
                selectedSemester:defaultSemid,

              },()=>{
                that.tableListRef.current.onSemesterChange(semesterData, defaultSemid);
                that.barchartRef.current.onSemesterChange(["OverallTest", defaultSemid]);
                that.heatmapRef.current.onSemesterChange(defaultSemid);
              })
            }
          })
        })
      }
    })
  }

  onSemesterChange(key, e){
    var semid = key.target.value ? key.target.value : 1100000
    var that = this

    getSemesterWiseStudentList(semid).then(semesterResponse => {
      if(semesterResponse.status === 200){
        var semesterData = semesterResponse.data;
        that.setState({
          studentListData:semesterData,
          selectedSemester:semid,

        },()=>{
          that.tableListRef.current.onSemesterChange(semesterData, semid);
          that.barchartRef.current.onSemesterChange(["OverallTest", semid]);
          that.heatmapRef.current.onSemesterChange(semid);
        })
      }
    })
  }

  onClickBack(){
    var that = this
    that.setState({
      semesterdata: [],
    },()=>{
      that.tableListRef.current.onClickBack();
      that.barchartRef.current.onClickBack();
      this.props.onReload();
    })

  }

  onStudentClick(param){
    console.log("TeacherDashboard - On student row select")
    this.props.onSelectedStudentClick(param);
  }


  render(){
    var that = this;
    var thatstate = this.state;

    let semesterOptionItems = that.state.semesterdata.map((op) =>
        <option key={op.Semester_ID} aria-label={op.Semester_Name} name={op.Semester_Name} value={op.Semester_ID}>{op.Semester_Name}</option>
    );

    return(
        <Box component="span" sx={{flexGrow: 1}}>
           <Grid container spacing={2}>
            <Grid container item direction="column" alignItems="center" justify="center">
              <Grid>
                <Button variant="outlined" onClick={that.onClickBack}>Back</Button>
              </Grid>
              <Grid >
                 <FormControl variant="outlined">
                  <InputLabel htmlFor="semesterid">Semester ID</InputLabel>
                  <Select
                      native
                      value={this.state.selectedSemester}
                      onChange={e => that.onSemesterChange(e, "key")}
                      label="Semester ID">
                    {semesterOptionItems}

                  </Select>
                </FormControl>
              </Grid>
              <Grid>
                <TableView
                    onSelectedStudentClick={that.onStudentClick}
                    studentList={thatstate.studentListData}
                    ref={that.tableListRef}
                />
              </Grid>
              <Grid >
                <BarChart
                    ref={that.barchartRef}
                />
              </Grid>
              <Grid >
                <Heatmap ref={that.heatmapRef}/>
              </Grid>
            </Grid>
          </Grid>
        </Box>
    )
  }
}
