import React from 'react';

import Plot from 'react-plotly.js';
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";

import {
  getOverallTestInformation,
  getSelesctedStudentTest,
  getSelfTestTaskAnalysis
} from "../api/utils";
import Button from "@mui/material/Button";

export default class BarChart extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      viewFlag: '',
      isSemesterSelected: false,
      isTaskSelected: false,
      testData: [],
      taskData: [],
      barChartTitle: '',
    };

    this.onSemesterChange = this.onSemesterChange.bind(this);
    this.perpareBarChartData = this.perpareBarChartData.bind(this);
    this.onClickBack = this.onClickBack.bind(this);
    this.onStudentSelect = this.onStudentSelect.bind(this);
    this.prepareTaskBarChartData = this.prepareTaskBarChartData.bind(this);
    this.onClickClose = this.onClickClose.bind(this);
  }

  onSemesterChange(param) {
    var that = this
    if (param[0] === "OverallTest") {
      getOverallTestInformation(param[1]).then(overallTestResponse => {
        if (overallTestResponse.status === 200) {
          var overallTestData = overallTestResponse.data;
          that.setState({
            testData: overallTestData,
            viewFlag: param[0],
            isSemesterSelected: true,
            barChartTitle: 'Overall test information'

          }, () => {
            var testData = {data: overallTestData, view: that.state.viewFlag}
            that.perpareBarChartData(testData)
          })
        }
      })
    }
  }

  perpareBarChartData(testData) {
    var barchartData = []
    var colorIndx = 0
    var color = ['#43a2ca', '#7bccc4', '#a8ddb5'] //'#0868ac'
    var groupname = ''

    if (testData['view'] === 'OverallTest') {
      groupname = 'Group_Name'
    } else {
      groupname = 'Type'
    }

    testData['data'].forEach((test) => {
      var testnames = test['test']['Test_Name']
      var pointPercentage = test['test']['Points(%)']
      var groupName = test['test'][groupname][0]
      var markerColor = color[colorIndx]

      var dataObj = {
        x: testnames,
        y: pointPercentage,
        name: groupName,
        type: 'bar',
        marker: {color: markerColor}
      }
      barchartData.push(dataObj)
      colorIndx = colorIndx + 1
    })
    this.setState({testData: barchartData})
  }

  prepareTaskBarChartData(taskData,testname){
    var barchartData = []
    var colorIndx = 0
    var color = ['#43a2ca', '#7bccc4', '#a8ddb5'] //'#0868ac'
    var groupname = ''

    taskData.forEach((task) => {

      var tasknames = task['task_details']['task_details']['Task_SID']
      var pointReached = task['task_details']['task_details']['Points_Reached']
      var groupName = "Attempt " + task['task_details']['attempt_no']
      var markerColor = color[colorIndx]

      var dataObj = {
        x: tasknames,
        y: pointReached,
        name: groupName,
        type: 'bar',
        marker: {color: markerColor}
      }
      barchartData.push(dataObj)
      colorIndx = colorIndx + 1
      debugger
    })
    this.setState({
      taskBarChartTitle: testname + ' task details information',
      isTaskSelected: true,
      taskData:barchartData
    })
  }

  onClickClose(){
    var that = this

    that.setState({
      isTaskSelected: false,
      taskData:[]
    })
  }

  handleClick = (event) => {
    var that = this

    if(that.state.viewFlag === "StudentTest"){
      const clickedBarData = event.points[0]
      console.log(clickedBarData)
      var testType = clickedBarData['data']['name']

      if(testType !== "Self Test"){
        alert("Currently, Task details for Self Test can be checked")
      }else{
        var testname = clickedBarData['label']
        var studentid = that.props.selectedStudent
        var semesterid = that.props.selectedSemester

        getSelfTestTaskAnalysis(semesterid,studentid,testname).then(taskAnalysisResponse =>{
          if (taskAnalysisResponse.status === 200) {
            var taskData = taskAnalysisResponse.data
            that.prepareTaskBarChartData(taskData,testname)
          }
        })
      }
    }
  }

  onClickBack() {
    var that = this
    that.setState({
      testData: [],
      viewFlag: '',
      isSemesterSelected: false,
      barChartTitle: '',
    })
  }

  onStudentSelect(param) {
    var that = this
    if (param[0] === "StudentTest") {
      getSelesctedStudentTest(param[1], param[2]).then(studentTestResponse => {
        if (studentTestResponse.status === 200) {
          var studentTestData = studentTestResponse.data;
          that.setState({
            testData: studentTestData,
            viewFlag: param[0],
            isSemesterSelected: true,
            barChartTitle: 'Student test information',
            open: false,

          }, () => {
            var testData = {data: studentTestData, view: that.state.viewFlag}
            that.perpareBarChartData(testData)
          })
        }
      })
    }
  }


  render() {
    var that = this
    const layout = {
      barmode: 'group',
      title: that.state.barChartTitle,
    };
    const tasklayout = {
      barmode: 'group',
      title: that.state.taskBarChartTitle,
    };
    const config = {
      displayModeBar: false, // Turn off the modebar
    };

    return (
        <Box component="span" sx={{flexGrow: 1}}>
          <Grid container spacing={2}>

            <Grid container item direction="column" alignItems="center" justify="center">
              {that.state.isSemesterSelected ? <Grid>
                <Plot
                    data={that.state.testData}
                    layout={layout}
                    style={{width: '100%', height: '400px'}}
                    config={config}
                    onClick={this.handleClick}
                />
              </Grid> : null}
              {that.state.isTaskSelected ?<Grid>
                {/*<Grid>*/}
                {/*  <Button variant="outlined" onClick={that.onClickClose}>Close</Button>*/}
                {/*</Grid>*/}
                {/*  <Grid>*/}
                  <Plot
                    data={that.state.taskData}
                    layout={tasklayout}
                    style={{width: '100%', height: '400px'}}
                    config={config}
                    // onClick={this.handleClick}
                />
              {/*</Grid>*/}
              </Grid>: null}
            </Grid>
          </Grid>
        </Box>
    )
  }
}
