import React from 'react';

import Plot from 'react-plotly.js';
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";

import {
  getCorrelationAmongTests
} from "../api/utils";

export default class Heatmap extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      testCorrelationData: [],
      isSemesterSelected: false,
    };

    this.onSemesterChange = this.onSemesterChange.bind(this);
    this.onClickBack = this.onClickBack.bind(this);
  }

  onSemesterChange(semesterid) {
    var that = this

    getCorrelationAmongTests(semesterid).then(correlationResponse => {
      if (correlationResponse.status === 200) {
        that.setState({
          testCorrelationData: correlationResponse.data,
          isSemesterSelected: true,

        })
      }
    })
  }

  onClickBack() {
    this.setState({
      testCorrelationData: [],
      isSemesterSelected: false,
    })
  }


  render() {

    var that = this

    const customColors = [
      [0, 'rgb(255, 255, 255)'],
      [0.2, 'rgb(226, 240, 217)'],
      [0.4, 'rgb(168, 221, 181)'],
      [0.6, 'rgb(123, 204, 196)'],
      [0.8, 'rgb(67, 162, 202)'],
      [1, 'rgb(8, 104, 172)']
    ];

    const config = {
      displayModeBar: false, // Turn off the modebar
    };


    if(that.state.testCorrelationData.length !== 0){
      var xLabels = Object.keys(that.state.testCorrelationData[0]);
      var yLabels = xLabels;

      var zValues = that.state.testCorrelationData.map(row => xLabels.map(label => row[label]));

      var heatmapData = {
        z: zValues,
        x: xLabels,
        y: yLabels,
        type: 'heatmap',
        colorscale: customColors,// Choose a colorscale of your preference
        // colorscale: 'Magma',
        // colorway: ['rgb(255, 255, 255)', 'rgb(226, 240, 217)', 'rgb(170, 215, 180)', 'rgb(102, 194, 165)', 'rgb(44, 162, 95)', 'rgb(0, 109, 44)'],
        zmin: -1, // Minimum value in your data
        zmax: 1,  // Maximum value in your data
      };


      var annotations = that.state.testCorrelationData.map((row, rowIndex) =>
          Object.keys(row).map((columnKey, columnIndex) => ({
            x: columnIndex,
            y: rowIndex,
            xref: 'x1',
            yref: 'y1',
            text: row[columnKey].toFixed(2),
            showarrow: false,
            font: {
              color: '#2D4356',
            },
          }))
      ).flat();

      var layout = {
        title: 'Corelation Between different tests',
        xaxis: {
          title: 'Test Type',
          tickfont: {
            size: 8,
          }
        },
        yaxis: {
          tickangle: 50,
          tickfont: {
            size: 8, // Set a smaller font size for x-axis labels
          }
        },
        width: 800,
        height: 800,
        annotations: annotations,
      };

    }


    return (
        <Box component="span" sx={{flexGrow: 1}}>
          <Grid container spacing={2}>
            <Grid container item direction="column" alignItems="center" justify="center">
              {that.state.isSemesterSelected ? <Grid>
                <Plot
                    data={[heatmapData]}
                    layout={layout}
                    config={config}
                />
              </Grid> : null}
            </Grid>
          </Grid>
        </Box>
    )
  }

}
