
import {
  getallSemesters
} from "../api/utils";

import React from 'react';
import {styled} from '@mui/material/styles';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Grid from "@mui/material/Grid";
import {DataGrid} from "@mui/x-data-grid";
import Button from '@mui/material/Button';





import TeacherDashboard from './TeacherDashboard'
import StudentDashboard from "./StudentDashboard";

export default class LandingView extends React.Component {
  teacherDashboardRef = React.createRef();
  studentDashboardRef = React.createRef();

  constructor(props) {
    super(props);
    this.state = {
      // visibility flags for different views
      isCommonView: true,
      isTeacherView: false,
      isStudentView: false,
      selectedSemester:'',
      semesterdata: [{'Semester_Name': 'Select One', 'Semester_ID': 100000}],
    };

    this.onClickTeacher = this.onClickTeacher.bind(this);
    this.onClickStudent = this.onClickStudent.bind(this);
    this.onReload = this.onReload.bind(this);
    this.onSelectedStudentClick = this.onSelectedStudentClick.bind(this);
    this.onClickStudentBack = this.onClickStudentBack.bind(this);
  }

  componentDidMount() {
    var that = this;

    getallSemesters().then((res) => {
      if (res.status === 200) {
        var data = res.data;
        data = [ {'Semester_Name': 'Select One', 'Semester_ID': 100000}, ...data]
        that.setState({semesterdata: data})
      }
    })
  }

  onClickTeacher() {
    this.setState({
      isCommonView: false,
      isTeacherView: true},()=>{
      this.teacherDashboardRef.current.onLoad();
    })
  }

  onClickStudent() {
    // console.log("hi i am a student")
  }

  onReload(){
    this.setState({
      isCommonView: true,
      isTeacherView: false,
    })
  }

  onSelectedStudentClick(param){
    console.log("LandingDashboard - On student row select")
    this.setState({
      selectedSemester:param['semesterid'],
      isTeacherView:false,
      isStudentView:true
    },()=>{
        this.studentDashboardRef.current.onLoad(param)
    })
  }

  onClickStudentBack(semesterid){
    console.log("Landing View - On Back")
    this.setState({
      isTeacherView:true,
      isStudentView:false
    },()=>{
      this.teacherDashboardRef.current.onReload(semesterid)
    })
  }

  render() {
    var that = this;
    var thatstate = this.state;

    return (
        <Box component="span" sx={{flexGrow: 1}}>
          <Grid container item direction="column">

          </Grid>
          <Grid container spacing={{xs: 2, md: 3}} columns={{xs: 4, sm: 8, md: 12}}>
            <Grid item xs={1}>
              <div style={{height: '25px'}}></div>
            </Grid>
            <Grid item xs={1}></Grid>
          </Grid>
          {this.state.isCommonView ? <Grid container spacing={2}>
            <Grid container item direction="column" alignItems="center" justify="center">
              <Grid item xs={4}>
                <Button variant="outlined" onClick={that.onClickTeacher}>Teacher</Button>
              </Grid>
              <Grid item xs={4}>
                <Button variant="outlined" onClick={that.onClickStudent}>Student</Button>
              </Grid>
            </Grid>
            <Grid item xs={1}></Grid>

            <Grid item xs={1}></Grid>
          </Grid> : null}
          {this.state.isTeacherView?
              <Grid><TeacherDashboard
                  onReload={this.onReload}
                  onSelectedStudentClick={that.onSelectedStudentClick}
                  ref={that.teacherDashboardRef}/> </Grid> : null}
          {this.state.isStudentView ?
              <Grid><StudentDashboard
                  selectedSemester={thatstate.selectedSemester}
                  onClickStudentBack={this.onClickStudentBack}
                  ref={that.studentDashboardRef}
              /></Grid> : null}

          {/*{this.state.isTeacherView ?  <Grid container spacing={2}>*/}
          {/*  <Grid container item direction="column" alignItems="center" justify="center">*/}
          {/*    <Grid >*/}
          {/*      <FormControl variant="outlined">*/}
          {/*        <InputLabel htmlFor="semesterid">Semester ID</InputLabel>*/}
          {/*        <Select*/}
          {/*            native*/}
          {/*            value={this.state.selectedSemester}*/}
          {/*            onChange={e => that.onSemesterChange(e, "key")}*/}
          {/*            label="Semester ID">*/}
          {/*          {semesterOptionItems}*/}

          {/*        </Select>*/}
          {/*      </FormControl>*/}
          {/*    </Grid>*/}
          {/*    <Grid >*/}
          {/*      <Plot*/}
          {/*          data={[heatmapData]}*/}
          {/*          layout={layout}*/}
          {/*          config={config}*/}
          {/*      />*/}
          {/*    </Grid>*/}
          {/*  </Grid>*/}
          {/*</Grid>: null}*/}
        </Box>
    );
  }
}

