import React from 'react';
import {styled} from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import {GridValueGetterParams, GridToolbar} from '@mui/x-data-grid';
import {
  DataGrid, GridRowsProp, GridColDef, GridToolbarContainer,
  GridToolbarColumnsButton, GridToolbarFilterButton
} from '@mui/x-data-grid';


export default class TableView extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      studentList: this.props.studentList,
      gridColDef: [],
      filters: {},
      isSemesterChanged:false,
      isStudentSelected:false,
      selectedStudentID: '',
      selectedSemester:''
    };

    this.onSemesterChange = this.onSemesterChange.bind(this);
    this.onClickBack = this.onClickBack.bind(this)
    // this.handleRowSelection = this.handleRowSelection.bind(this);
  }

  onSemesterChange(studentdata, semesterid){
    var that = this

    that.setState({
      selectedSemester: semesterid,
      filters: {},
      isSemesterChanged:true,
      gridColDef: [
        {field: 'Semester_Name', headerName: 'Semester Name', width: 150},
        {field: 'MatrikelNumber', headerName: 'Matrikel Number', width: 150},
        {field: 'Lastname', headerName: 'Firstname', width: 150},
        {field: 'Surname', headerName: 'Surname', width: 150},
        {field: 'EMail', headerName: 'E-Mail', width: 150},
      ],
      studentList:studentdata
    })
  }

  onClickBack(){
    var that = this
    that.setState({
      filters: {},
      isSemesterChanged:false,
      gridColDef: [],
      studentList:[]
    })
  }

  handleRowSelection = (selectionModel) => {
    console.log("TableView - On student row select")
    var that = this
    this.setState({
      selectedStudentID: selectionModel[0],
      isStudentSelected:true  }, () =>{
      var param = {
        'semesterid':this.state.selectedSemester,
        'view':'StudentList',
        'studentid': this.state.selectedStudentID
      }
      this.props.onSelectedStudentClick(param);
    });

    // Your logic here...
  };

  handleFilterChange = (field, value) => {
    this.setState((prevState) => ({
      filters: {
        ...prevState.filters,
        [field]: value,
      },
    }));
  };

  componentDidMount() {
  }

  CustomToolbar() {
    return (
        <GridToolbarContainer>
          <GridToolbarFilterButton/>
        </GridToolbarContainer>
    )
  }


  render() {
    var that = this
    const { filters } = this.state;

    const filteredRows = that.state.studentList.filter((row) => {
      for (const field in filters) {
        if (row[field] !== filters[field]) {
          return false;
        }
      }
      return true;
    });

    const Item = styled(Paper)(({theme}) => ({
      backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
      ...theme.typography.body2,
      padding: theme.spacing(1),
      textAlign: 'center',
      color: theme.palette.text.secondary,
    }));

    return (
        <Box component="span" sx={{flexGrow: 1}}>
          <Grid container spacing={{ xs: 2, md: 3 }} columns={{ xs: 4, sm: 8, md: 12 }}>
            <Grid item xs={1}> <div style={{height: '25px'}}></div> </Grid>
            <Grid item xs={1}></Grid>
          </Grid>
          {that.state.isSemesterChanged ? <Grid container spacing={1}>
            <Grid item xs={1}></Grid>
            <Grid>
              <DataGrid
                  rows= {that.state.studentList}
                  columns={that.state.gridColDef}
                  slots={{toolbar: () => this.CustomToolbar()}}
                  initialState={{
                    pagination: { paginationModel: { pageSize: 5 } },
                  }}
                  pageSizeOptions={[5, 10, 15]}
                  components={{Toolbar: GridToolbar}}
                  onFilterChange={this.handleFilterChange}
                  onRowSelectionModelChange={this.handleRowSelection}
              />
            </Grid>
            <Grid item xs={1}></Grid>
          </Grid>: null}
        </Box>
        // <Box sx={{ flexGrow: 1 }}>
        //   <Grid container spacing={2}>
        //     <Grid item xs={8}>
        //       <Item>xs=8</Item>
        //     </Grid>
        //     <Grid item xs={4}>
        //       <Item>xs=4</Item>
        //     </Grid>
        //     <Grid item xs={4}>
        //       <Item>xs=4</Item>
        //     </Grid>
        //     <Grid item xs={8}>
        //       <Item>xs=8</Item>
        //     </Grid>
        //   </Grid>
        // </Box>
    );
  }
}
